import {fetch} from '@/utils/fetch';
/**
 * 公告列表接口
 * @param {*}  params
 */
export const  afficheList= (params) => fetch({
  url: 'api/corpMessage/selectSentMessage',
  method: 'post',
  data: params
});
/**
 * 公告详情列表接口
 * @param {*}  params
 */
export const  afficheDetailList= (params) => fetch({
  url: 'api/public/corpMessage/selectHtmlContent',
  method: 'get',
  data: params
});
/**
 * 公告已读接口
 * @param {*}  params
 */
export const  afficheRead= (params) => fetch({
  url: 'i/api/corpMessage/setChannelMessageRead',
  method: 'post',
  data: params
});



