import {afficheList} from '@/api/afficheDetail'
import {afficheRead} from '@/api/afficheDetail'

export default {
  name: 'affiche',
  components: {},
  data() {
    return {
        afficheList:"",
        afficheRead:"",
    }
  },
  methods: {
    afficheDetail(item){
      console.log("111");
      this.getAfficheRead(item.corpMessageId);
      this.$router.push({
        name: 'afficheDetail',
        params: {detailList:item},
      });
    },
    getAfficheList(){//获得公告列表接口数据
      afficheList({"channel":["25879380-fb2a-4f7b-a101-34586e1e51c5"]}).then(response => {
        console.log(222);
        if (response.data && response.data.rows) {
          this.afficheList = response.data.rows;
          this.isLoadingData = false;
        } else {
          this.isLoadingData = false;
        }
      }).catch(error => {
        this.isLoadingData = false;
      });
    },
    getAfficheRead(params){//获得公告已读接口数据
      afficheRead({"channel":params}).then(response => {
        this.afficheRead = response.data;
        console.log("哈哈",this.afficheRead.success);
        this.isLoadingData = false;
      }).catch(error => {
        this.isLoadingData = false;
      });
    },
  },
  mounted() {
    this.getAfficheList();
  }
}




